import React, { Fragment } from 'react';
import {IGallery, IPaint, IInfo} from "../../entity/gallery.class";
import Gallery from "./art.gallery"

interface Props{
    gallerie:Array<IGallery>,
    info:IInfo,
    setsections:Function
}

const Index:React.FC<Props> = props => {
    const setSectionNew = ():IGallery => {
        const LastYearOfNews = new Date().getUTCFullYear()-props.info.nouveaute_plage_annuel;
        let paints:Array<IPaint> = []
        props.gallerie.map(g =>{
            g.peintures.map(paint =>{
                if(paint.peinture_id.annee_de_creation 
                && paint.peinture_id.annee_de_creation >= LastYearOfNews){
                    !paints.includes(paint) && paints.push(paint);
                    return true
                }
                else return false;
            });
            return true;
        });
        return {
            titre:"Nouveautés",
            description:props.info.description_page_nouvelle_peintures,
            miniature:{
                filename_disk:paints[0] && paints[0].peinture_id.photo.filename_disk
            },
            peintures:paints,
            color:"#40B4D8"
        }
    }
    props.setsections(
        <Fragment>
            <Gallery 
            id={0} 
            gal={setSectionNew()} 
            last={false} 
            key={"news0"}/>
          {props.gallerie.map((g,i) => {
            return <Gallery 
            id={(i+1)} 
            gal={g} 
            last={i+1 === props.gallerie.length?true: false} 
            key={g.titre+(i+1)}/>
          })}
        </Fragment>
      );
  return (
    <Fragment>
        
        <div className="artIndexInfo">
            <div className="artTitle">
                <h1>Lecointe<br/>Christine</h1>
            </div>
            <div className="artContent">
                <p>{props.info.description_art}</p>
            </div>
            
        </div>
        <div className="artLinks">
            <a 
            href={"#gallery0"} 
            style={{backgroundColor: "#40B4D8"}}
            >Nouveautés</a>
            {props.gallerie.map((g,i) => {
                return (
                <a 
                href={"#gallery"+(i+1)} 
                key={"link"+i}
                style={{backgroundColor: g.color}}
                >{g.titre}</a>
                );
            })}
        </div>
        
        
    </Fragment>
  );
}

export default Index;
