import { request, gql } from 'graphql-request'

export interface IPaint{
    peinture_id:{
        titre:string
        description:string|null
        photo:{
        filename_disk:string
        }
        annee_de_creation:number|null
        technique_de_creation:string|null
        }
  }
export interface IGallery{
    titre:string,
    description:string|null,
    miniature:{
        filename_disk:string
      }
    peintures:Array<IPaint>
    color:string;
    
}
export interface IInfo{
    id:number,
    description_art:string,
    description_yoga:string,
    description_index:string,
    description_page_nouvelle_peintures:string,
    nouveaute_plage_annuel:number
}
export interface IYoga{
    titre:string,
    description:string,
    thumbnail: {
        filename_disk:string,
    }
    etapes: Array<{
        titre:string,
        description:string,
        postures:Array<{
        titre:string,
        mouvements:Array<{
            description:string,
            etapes_img:Array<{
            directus_files_id:{
                filename_disk:string,
            }
            }>
        }>
        }>
        sub_etapes:{
        titre:string,
        description:string,
        postures:Array<{
            titre:string,
            mouvements:Array<{
            description:string,
            etapes_img:Array<{
            directus_files_id:{
                filename_disk:string,
            }
            }>
            }>
        }>
        }
    }>
}
export class Gallery{

    

    static async getGallerys():Promise<{galerie:Array<IGallery>,info:IInfo,fiche_yoga:any}>{

        let galerie:Array<IGallery> = []
        let info={
            id:0,
            description_art:"",
            description_yoga:"",
            description_index:"",
            description_page_nouvelle_peintures:"",
            nouveaute_plage_annuel:0
        }
        let yoga: Array<IYoga> =[];
        const query = gql`
        {
            galerie (filter:{status: {_eq: "published"}}, sort:"date_updated") {
              titre
              description
              status
              color
              miniature{
                  filename_disk
                }
              peintures{
                peinture_id (sort:"annee_de_creation") {
                  titre
                  description
                  photo{
                    filename_disk
                  }
                  annee_de_creation 
                  technique_de_creation
                }
              }
            }
            fiche_yoga (filter:{status: {_eq: "published"}}) {
              titre
              description
              thumbnail {
                filename_disk
              }
              etapes {
                titre
                description
                postures{
                  titre
                  mouvements{
                    description
                    etapes_img{
                      directus_files_id{
                        filename_disk
                      }
                    }
                  }
                }
                sub_etapes{
                  titre
                  description
                  postures{
                    titre
                    mouvements{
                    description
                      etapes_img{
                      directus_files_id{
                        filename_disk}
                      }
                    }
                  }
                }
              }
            }
            infos_generale {
                id
                description_art
                description_yoga
                description_index
                description_page_nouvelle_peintures
                nouveaute_plage_annuel
              }
          }
        `;
        await request('https://christineapi.chiliwa.com/graphql', query)
        .then((data:{galerie:Array<IGallery>,infos_generale:IInfo,fiche_yoga:Array<IYoga>}) => {
            galerie=data.galerie;
            info=data.infos_generale;
            yoga = data.fiche_yoga;
            return {galerie:galerie,info:info,fiche_yoga:yoga};
        })
        .catch(er => console.log("error: "+er));
        return {galerie:galerie,info:info,fiche_yoga:yoga};
    }
}