import React, { Fragment } from 'react';
import {IYoga} from "../../entity/gallery.class";
import Card from "./yoga.card";
import Video from "./yoga.video";

interface Props{
  info:string|undefined,
  ficheYoga:Array<IYoga>,
  setsections:Function
}

const Index:React.FC<Props> = props => {
  props.setsections(
    <Fragment>
      <Card 
        id={0} 
        name={"fiches techniques"} 
        content={"fiche.description"} 
        last={false} />
      <Video
        id={1} 
        name={"video youtube"} 
        content={"fiche.description"} 
        last={true} />
    </Fragment>
  )
  return (
    <Fragment>
        <div className="yogaNav">
          <a href={"#yoga0"} className="BtnNext yogaBox">Fiches techniques</a>
          <a href={"#yoga1"} className="BtnNext yogaBox">videos explicatives</a>
        </div>
        <div className="yogaIndexInfo yogaBox">
          <div className="yogaTitle">
              <h1>Lecointe<br/>Christine</h1>
          </div>
          <div className="yogaContent">
              <p>{props.info!==undefined && props.info}</p>
          </div>
        </div>
    </Fragment>
  );
}

export default Index;
