import React from 'react';
import {IPaint, IGallery} from "../../entity/gallery.class";

interface Props{
    id:number,
    last:boolean
    gal:IGallery
}

const slider = (direction:"left"|"right", classPaint:string) => {
    const paints = Array.from(document.getElementsByClassName(classPaint)) as Array<HTMLElement>;
    paints.sort((a,b)=> +a.style.order - +b.style.order)
    direction === "right"
        && (paints[paints.length-1].style.order = (+paints[0].style.order - 1).toString());
    direction === "left"
        && (paints[0].style.order = (+paints[paints.length-1].style.order + 1).toString());
    paints.sort((a,b)=> +a.style.order - +b.style.order);
    let newTitle = paints[Math.ceil((paints.length-1)/2)].getAttribute("data-titre");
    (document.getElementsByClassName("sliderNamePaint"+classPaint)[0] as HTMLElement)
        .innerHTML = newTitle?newTitle:"";
    paints[Math.ceil((paints.length-1)/2)].classList.contains("paintGalleryEmpty")
        && slider(direction,classPaint);
    
}

const zoom = (id:string, img:string, title:string, desc:string|null, tech:string|null, realisation:number|null) => {
    const zoomZone = document.querySelector(`#${id} .zoomImg`) as HTMLElement;
    zoomZone.style.background = `url("${img}"),rgba(0, 0, 0, 0.507)`;
    zoomZone.style.backgroundSize = "contain";
    zoomZone.style.backgroundRepeat="no-repeat";
    zoomZone.style.backgroundPosition="center";
    (document.querySelector(`#${id} .zoomInfo`) as HTMLElement).innerHTML = `
    <h2>${title}</h2>
    <p class="subTitle">${realisation?realisation+" - ":""}${tech?tech:""}</p>
    <p>${desc?desc:""}</p>
    `;
    (document.getElementById(id) as HTMLElement).classList.remove("hide")
}
const hide = (id:string) => {
    (document.getElementById(id) as HTMLElement).classList.add("hide")
}

const Gallery:React.FC<Props> = props => {
    return (
    <section key={"keyGallery"+props.id} className="gallerySection" id={"gallery"+(props.id)} style={{background: `url("${`https://christineapi.chiliwa.com/assets/`+props.gal.miniature.filename_disk}")`,backgroundSize: "cover"}}>
        <div className="galleryTxt">
            <h2 className="galleryTitle">{props.gal.titre}</h2>
            <p className="galleryContent">{props.gal.description}</p>
        </div>
        
        <div  className="zoomPicGallery hide" id={"zoomPicGallery"+props.id}>
            <div className="zoomImg"></div>
            <p onClick={()=>{hide("zoomPicGallery"+props.id)}}>✖</p>
            <div className="zoomInfo"></div>
            
        </div>
        <div className="gallery">
            <div className="galleryPrev BtngalInter" onClick={()=>{slider("right","paintGallery"+props.id)}}><i className="fas fa-caret-left"></i></div>
            <div className="galleryNext BtngalInter" onClick={()=>{slider("left","paintGallery"+props.id)}}><i className="fas fa-caret-right"></i></div>
            <div className="slider">
            {props.gal.peintures.map((paint:IPaint,i) =>{
                return <div 
                    key={props.id+"paint"+i} 
                    style={{background: `url("${`https://christineapi.chiliwa.com/assets/`+paint.peinture_id.photo.filename_disk}")`,backgroundSize: "contain",order:i,backgroundRepeat:"no-repeat",backgroundPosition:"center"}} 
                    data-titre={paint.peinture_id.titre}
                    className={"paintGallery paintGallery"+props.id}
                    onClick={()=>{zoom("zoomPicGallery"+props.id,`https://christineapi.chiliwa.com/assets/`+paint.peinture_id.photo.filename_disk,paint.peinture_id.titre,paint.peinture_id.description,paint.peinture_id.technique_de_creation,paint.peinture_id.annee_de_creation)}}>
                </div>
            })}
            {props.gal.peintures.length % 2 !== 1 && <div className={"paintGalleryEmpty paintGallery"+props.id} style={{order:props.gal.peintures.length}}></div>}
            </div>
        </div>
        <p className={"sliderNamePaint sliderNamePaintpaintGallery"+props.id}>
            {props.gal.peintures[Math.ceil((props.gal.peintures.length-1)/2)].peinture_id.titre}
        </p>

        <a href={props.id !== 0?"#gallery"+(props.id-1):"#index"} className="BtnPrev BtnArt"><i className="fas fa-arrow-up"></i></a>
        <a href={"#index"} className="BtnIndex BtnPrev  BtnArt"><i className="fas fa-angle-double-up"></i></a>
        <a href={"#gallery"+(props.id+1)} className={props.last?"BtnNext hide":"BtnNext BtnArt"}><i className="fas fa-arrow-down"></i></a>
    </section>
  );
}

export default Gallery;
